import {
	mapState
} from 'vuex'
import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiBadge from "@/components/badge/badge"
import tuiNomore from "@/components/nomore/nomore"
import tuiButton from "@/components/button/button"
import tuiTopDropdown from "@/components/top-dropdown/top-dropdown"
import tuiBottomPopup from "@/components/bottom-popup/bottom-popup"
import tuiNumberbox from "@/components/numberbox/numberbox"
export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiBadge,
		tuiNomore,
		tuiButton,
		tuiTopDropdown,
		tuiBottomPopup,
		tuiNumberbox
	},
	data() {
		return {
			goodsInfo: {},
			showCont: true,
			height: 64, //header高度
			top: 0, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			iconOpcity: 0.5,
			banner: [],
			bannerIndex: 0,
			topMenu: [{
				icon: "home",
				text: "首页",
				size: 23,
				badge: 0
			}, 
			{
				icon: "people",
				text: "我的",
				size: 26,
				badge: 0
			}, 
			{
				icon: "cart",
				text: "购物车",
				size: 23,
				badge: 2
			}
			],
			menuShow: false,
			popupShow: false,
			value: 1,
			collected: false
		}
	},
	methods: {
		bannerChange: function(e) {
			this.bannerIndex = e.detail.current
		},
		previewImage: function(e) {
			let picArray = [];
			this.banner.map(item => {
				picArray.push(item.url);
			});
			let index = e.currentTarget.dataset.index;
			uni.previewImage({
				current: this.banner[index].url,
				urls: picArray
			})
		},
		handleChangeCont(num){
			switch (num) {
				case 0:
					this.showCont=true;
					break;
				case 1:
					this.showCont=false;
					break;
				default:
					this.showCont=true;
			}
		},
		back: function() {
			uni.navigateBack()
		},
		openMenu: function() {
			this.menuShow = true
		},
		closeMenu: function() {
			this.menuShow = false
		},
		showPopup: function() {
			this.popupShow = true
		},
		hidePopup: function() {
			this.popupShow = false
		},
		change: function(e) {
			this.value = e.value
		},
		handleAddCart(mallGoodsId){
			if(!this.hasLogin){
				uni.navigateTo({
				    url: '/pages/login/login'
				});
			}else{
				this.$http.post('/mall/app/car/add', {
					...this.baseInfo,
					accountId: this.userInfo.accountId,
					goodsCount: this.value,
					mallGoodsId: mallGoodsId
				})
				.then( res => {
					if(res.code == 0){
						this.getGoodsDetail();
						uni.showToast({
							icon: 'none',
							title: '商品添加成功',
						});
					}else{
						console.log('productDetail.vue-- add接口添加购物车失败');
					}
				})
				.catch( err => {
					console.log('productDetail.vue-- add接口添加购物车错误');
				})
			}
			this.hidePopup();
		},
		linkToPayment(id){
			if(!this.hasLogin){
				uni.navigateTo({
				    url: '/pages/login/login'
				});
			}else{
				let tmpList = [];
				tmpList.push({
					mallGoodsId: id,
					goodsCount: this.value
				})
				uni.setStorage({
					key:'buyList',
					data:tmpList,
					success: () => {
						uni.navigateTo({
							url:'/pages/payment/payment'
						})
					}
				})
			}
			this.hidePopup();
		},
		// collecting: function() {
		// 	this.collected = !this.collected
		// },
		// common: function() {
		// 	this.tui.toast("功能开发中~")
		// },
		linkToIndex(){
			uni.reLaunch({
				url: '/pages/index/index'
			})
		},
		linkToUser(){
			uni.reLaunch({
				url: '/pages/user/user'
			})
		},
		linkToCart(){
			if(!this.hasLogin){
				uni.showToast({
					icon: 'none',
					title: '请先去登陆吧~'
				})
			}else{
				uni.reLaunch({
					url: '/pages/car/car'
				})
			}
		},
		getGoodsDetail(){
			let goodsId=uni.getStorageSync('goodsId');
			this.$http.post('/mall/app/goods/detail', goodsId)
			.then( res => {
				// console.log(res);
				if(res.code == 0){
					this.goodsInfo=res.result;
					this.banner=res.result.pics;
				}else{
					console.log('productDetail.vue-- detail接口获取数据失败');
				}
			})
			.catch( err => {
				console.log('productDetail.vue-- detail接口获取数据错误');
			})
		}
	},
	computed: {
		...mapState('common', ['baseInfo', 'userInfo', 'hasLogin'])
	},
	onLoad: function(options) {
		uni.setStorageSync('goodsId', options);
		this.getGoodsDetail();
		
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







	
		uni.getSystemInfo({
			success: (res) => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
				this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
				this.scrollH = res.windowWidth
			}
		})
	},
	// onPullDownRefresh() {
	// 	console.log('refresh');
	// 	setTimeout(function () {
	// 		uni.stopPullDownRefresh();
	// 	}, 1000);
	// },
	onPageScroll(e) {
		let scroll = e.scrollTop <= 0 ? 0 : e.scrollTop;
		let opcity = scroll / this.scrollH;
		if (this.opcity >= 1 && opcity >= 1) {
			return;
		}
		this.opcity = opcity;
		this.iconOpcity = 0.5 * (1 - opcity < 0 ? 0 : 1 - opcity)
	}
}
